.haloo-sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 15vw;
    -webkit-box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.16), 0px 3px 10px rgba(0, 0, 0, 0.23);
            box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.16), 0px 3px 10px rgba(0, 0, 0, 0.23);
}

.sidebar-header {
    font-family: "Roboto";
    font-weight: 500;
    letter-spacing: 1px;
    font-size: 1.25em;
    background-color: rgb(0, 188, 212);
    color: white;
    padding: 10px 15px;
}

.message-preview {
    padding: 10px 15px;
    font-family: "Roboto";
    cursor: pointer;
}

.message-preview-header {
    font-weight: 500;
    letter-spacing: 0.5px;
    font-size: 18px;
    margin-bottom: 5px;
    cursor: pointer;
}

.message-preview-text {
    font-size: 16px;
    color: #9E9E9E;
    line-height: 1em;
    max-width: 100%;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    cursor: pointer;
}


